body{
    background-color: #c0d2d4;
    font-weight: 400;
    font-family:  "montserrat"sans-serif;
    line-height: 1.666;
    color:#555;
}
h1{
    font-weight: 900;
    text-align: center;
    color: #fff;
    font-size: 500%;
    
}
