    padding: 15px 25px;
    background-color:#2ec2b0;
}
.dictionary{
    
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 4rem auto;
    max-width: 90%;
    height: 200px;
    margin-top: 100px;
    
}
.emoji{
    display: block;
}

