dd{
    font-size: 92.5%;
}
dt span{
    font-size: 200%;
    font-weight: 900;
    color:#2ec2b0 ;
    text-align: center;
}
dt{
    text-align: center;
    padding: 25px;
}
.dictionary > .term{
    text-align: center;
    max-width: 20rem;
    margin-bottom: 3rem;
    padding: 2rem;
    border-radius: 25px;
    box-shadow: 0 2px 0 rgba(0,0,0,0.05),0 5px 20px rgba(0,0,0,0.1);
    background-color: #fff;
    transition: all 100ms ease-in-out;
}




